@mixin font-face($font-family, $url, $weight) {
  @font-face {
    font-family: "#{$font-family}";
    src: url('../fonts/#{$url}.woff2') format("woff2"),
         url('../fonts/#{$url}.woff') format("woff"),
         url('../fonts/#{$url}.ttf') format("ttf");
    font-weight: #{$weight};
    font-display: swap;
    font-style: normal;
  }
}
